<template>
  <div class="hrss-per attendance-info" v-loading="loading">
    <div class="hrss-search">
      <el-input placeholder="考勤期间" prefix-icon="el-icon-search" v-model="selectDate" readonly @click.native="selectDatePopup=true" size="medium">
        <i slot="suffix" class="el-input__icon el-icon-caret-bottom"></i>
      </el-input>
      <mt-popup v-model="selectDatePopup" closeOnClickModal="true" position="bottom">
        <mt-picker :slots="dataSlots" @change="onValuesChange" showToolbar valueKey="CAL_PRD_DESCR">
          <div class="picker-toolbar-title">
            <div class="usi-btn-cancel" @click="selectDatePopup=!selectDatePopup">取消</div>
            <div class="">考勤期间</div>
            <div class="usi-btn-sure" @click="getSalary">确定</div>
          </div>
        </mt-picker>
      </mt-popup>
    </div>
    <el-collapse v-model="activeNames">
      <el-collapse-item v-if="attList.length > 0" v-for="att in attList" :key="att.SECTIONS_CD" :title="att.SECTIONS_DESCR" :name="att.SECTIONS_CD">
        <ul class="hrss-item-ul" v-if="att.ITEMS.length > 0">
          <li v-for="(item,index) in att.ITEMS" :key="index">
            <div class="hrss-item-table social-item active">
              <div>
                <span>类型</span>
                <span class="hrss-item-right">{{item.NAME}}</span>
              </div>
              <div>
                <span>时长</span>
                <span class="hrss-item-right">{{item.VALUE}}</span>
              </div>
              <div>
                <span>单位</span>
                <span class="hrss-item-right">{{item.UNIT}}</span>
              </div>
            </div>
          </li>
        </ul>
        <span v-if="att.ITEMS.length == 0" class="no-data">暂无数据</span>
      </el-collapse-item>
      <span v-if="attList.length == 0" class="no-data">暂无数据</span>
    </el-collapse>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeNames: [],
      selectDatePopup: false,
      selectDate: "",
      dataSlots: [],
      slotsSelect: [],
      personal: {},
      prdType: "ATT",
      attList: [],
      loading: false
    };
  },
  methods: {
    onValuesChange(picker, values) {
      this.slotsSelect = values;
    },
    getSalary() {
      if (this.slotsSelect.length > 0)
        this.selectDate = this.slotsSelect[0].CAL_PRD_DESCR;
      this.selectDatePopup = false;
      this.getAttList();
    },
    getPrdList() {
      let p = {
        TOKEN: this.getCookie("token"),
        OPRID: this.getCookie("oprid"),
        PRD_TYPE: this.prdType
      };
      this.$http
        .post("/PSIGW/RESTListeningConnector/PSFT_HR/PayRoll_Period.v1", p)
        .then(res => {
          if (res.body.status == "0") {
            let d = res.body.JsonData;
            if (d.length > 0) {
              this.dataSlots = [{ values: d }];
              this.slotsSelect = [d[0]];
              this.getSalary();
            }
          }
        });
    },
    getAttList() {
      this.loading = true;
      let p = {
        TOKEN: this.getCookie("token"),
        OPRID: this.getCookie("oprid"),
        PRD_TYPE: this.prdType,
        CALPRDID: this.slotsSelect[0].CAL_PRD_ID
      };
      this.$http
        .post("/PSIGW/RESTListeningConnector/PSFT_HR/PAYROLL_SELECT.v1", p)
        .then(res => {
          if (res.body.status == "0") {
            this.attList = res.body.JsonData[0].SECTIONS;
            this.activeNames = [];
            this.attList.forEach(e => {
              this.activeNames.push(e.SECTIONS_CD);
            });
          }
          this.loading = false;
        }).catch(e =>{
          this.loading = false;
        });
    }
  },
  created() {
    this.personal = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getPrdList();
  }
};
</script>

<style>
</style>


