<template>
    <div class="recruit-info">
        <h2>招贤纳士详情</h2>
        <el-collapse v-model="activeNames" v-loading="loading">
            <el-collapse-item title="招聘职位信息" name="1">
                <table class="info-view">
                    <tr>
                        <td>招聘职位</td>
                        <td>{{recruitData.POSITION}}</td>
                    </tr>
                    <tr>
                        <td>招聘单位</td>
                        <td>{{recruitData.BUSINESS_UNIT}}</td>
                    </tr>
                    <tr>
                        <td>工作地点（城市）</td>
                        <td>{{recruitData.LOCATION}}</td>
                    </tr>
                    <tr>
                        <td>职位类别</td>
                        <td>{{recruitData.JOBFUNCTION}}</td>
                    </tr>
                    <tr>
                        <td>用人部门</td>
                        <td>{{recruitData.DEPT}}</td>
                    </tr>
                    <tr>
                        <td>用工性质</td>
                        <td>{{recruitData.GC_EMPLY_NATR}}</td>
                    </tr>
                    <tr>
                        <td>招聘人数</td>
                        <td>{{recruitData.GC_RE_COUNT}}</td>
                    </tr>
                    <tr>
                        <td>直接上级员工</td>
                        <td>{{recruitData.SUP_NAME}}</td>
                    </tr>
                    <tr>
                        <td>职位月薪</td>
                        <td>{{recruitData.GC_RE_POSN_SALER}}</td>
                    </tr>
                    <tr>
                        <td>职位职责</td>
                        <td>{{recruitData.GC_RE_POSN_DUTY}}</td>
                    </tr>
                </table>
            </el-collapse-item>
            <el-collapse-item title="任职要求" name="2">
                <table class="info-view">
                    <tr>
                        <td>学历要求</td>
                        <td>{{recruitData.CG_EDU_BCKG}}</td>
                    </tr>
                    <tr>
                        <td>专业要求</td>
                        <td>{{recruitData.GC_MAJOR}}</td>
                    </tr>
                    <tr>
                        <td>相关工作经验年限</td>
                        <td>{{recruitData.GC_WORK_YEARS}}</td>
                    </tr>
                    <tr>
                        <td>语种要求</td>
                        <td>{{recruitData.LANGUAGE}}</td>
                    </tr>
                    <tr>
                        <td>语种等级</td>
                        <td>{{recruitData.GC_LANG_SKILEXP}}</td>
                    </tr>
                    <tr>
                        <td>期望到岗日期</td>
                        <td>{{recruitData.GC_ARRV_POSN_DT}}</td>
                    </tr>
                    <tr>
                        <td>简历投递截止日期</td>
                        <td>{{recruitData.GC_END_DT}}</td>
                    </tr>
                    <tr>
                        <td>任职资格</td>
                        <td>{{recruitData.GC_POSN_QUAL}}</td>
                    </tr>
                    <tr>
                        <td>其他信息</td>
                        <td>{{recruitData.COMMENT}}</td>
                    </tr>
                </table>
            </el-collapse-item>
            <el-collapse-item title="申请职位" name="3" v-if="recruitData.POSITION_NBR">
                <table class="info-view apply-info">
                    <tr>
                        <td>国家/证件类型</td>
                        <td>
                            <el-input class="selectInput" placeholder="请选择" v-model="selectShow" readonly @click.native="selectDatePopup=true" size="small">
                                <i slot="suffix" class="el-input__icon el-icon-caret-bottom"></i>
                            </el-input>
                            <mt-popup v-model="selectDatePopup" closeOnClickModal="true" position="bottom">
                                <mt-picker :slots="dataSlots" @change="onValuesChange" showToolbar valueKey="name">
                                    <div class="picker-toolbar-title">
                                        <div class="usi-btn-cancel" @click="selectDatePopup=!selectDatePopup">取消</div>
                                        <div class="">国家/证件类型</div>
                                        <div class="usi-btn-sure" @click="getSalary">确定</div>
                                    </div>
                                </mt-picker>
                            </mt-popup>
                        </td>
                    </tr>
                    <tr>
                        <td>证件号码</td>
                        <td>
                            <el-input class="selectInput" placeholder="证件号码" v-model="applyData.NATIONAL_ID" size="small">
                            </el-input>
                        </td>
                    </tr>
                </table>
                <button class="submit-apply" @click="submitApply">提交申请</button>
            </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script>
import Field from "@/assets/js/field";
export default {
  data() {
    return {
      activeNames: ["1", "2", "3"],
      recruitData: {},
      applyData: {
        TOKEN: this.getCookie("token"),
        POSITION_NBR: "",
        IS_AGAIN: "N"
      },
      loading: false,
      selectDatePopup: false,
      dataSlots: [
        {
          values: Field,
          defaultIndex: 6
        },
        {
          divider: true,
          content: "-",
          className: "slot2"
        },
        {
          values: Field[6].child,
          defaultIndex: 0
        }
      ],
      selectValues: [],
      selectShow: ""
    };
  },
  methods: {
    onValuesChange(picker, values) {
      //   console.log(values);
      if (!values[0]) {
        this.$nextTick(() => {
          if (this.myAdress) {
            // 赋默认值
          } else {
            picker.setValues([Field[6], Field[6].child]);
          }
        });
      } else {
        picker.setSlotValues(1, values[0].child);
      }
      this.selectValues = values;
    },
    getSalary() {
      this.selectDatePopup = false;
      if (this.selectValues.length > 0) {
        this.applyData.COUNTRY = this.selectValues[0].value;
        this.applyData.NID_TYPE = this.selectValues[1].value;
        this.selectShow =
          this.selectValues[0].name + "/" + this.selectValues[1].name;
      }
    },
    submitApply() {
      if (!this.applyData.COUNTRY) {
        this.$messagebox.alert("国家/证件类型为空");
        return;
      }
      if (!this.applyData.NATIONAL_ID) {
        this.$messagebox.alert("证件号为空");
        return;
      }
      this.loading = true;
      this.$http
        .post(
          "/PSIGW/RESTListeningConnector/PSFT_HR/RecuitSave.v1/",
          this.applyData
        )
        .then(function(response) {
          if (response.body.status == "0") {
            this.$messagebox.alert("提交成功").then(() => {
              this.$router.push({ name: "recruit" });
            });
          } else if (response.body.status == "2") {
            this.$messagebox
              .confirm(response.body.message)
              .then(() => {
                this.applyData.IS_AGAIN = "Y";
                this.submitApply();
              })
              .catch(err => {
                // this.$router.push({ name: "recruit" });
                this.loading = false;
              });
          } else {
            this.$messagebox.alert(response.body.message);
            this.loading = false;
          }
        });
    },
    getRecruitData(id) {
      if (!id) return;
      this.loading = true;
      let p = {
        TOKEN: this.getCookie("token"),
        ID: id
      };
      this.$http
        .post("/PSIGW/RESTListeningConnector/PSFT_HR/RecuitInfo.v1/", p)
        .then(function(response) {
          if (response.body.status == "0") {
            this.recruitData = response.body;
            this.applyData.POSITION_NBR = this.recruitData.POSITION_NBR;
          }
          this.loading = false;
        })
        .catch(function(error) {
          this.loading = false;
          console.log(error);
        });
    }
  },
  created() {
    let id = this.$route.params.id;
    if (id) {
      this.getRecruitData(id);
    }
  }
};
</script>

<style>
.recruit-info h2 {
  font-weight: bolder;
  font-size: 19px;
  color: #736b6b;
  width: 100%;
  margin: auto;
  position: fixed;
  top: 0rem;
  background-color: #f8f8f8;
  z-index: 999;
  padding: 0.2rem 0.2rem;
}
.recruit-info .el-collapse {
  margin-top: 0.8rem;
  margin-bottom: 1rem;
}
.el-collapse-item .el-collapse-item__header {
  border-top: 1px solid #ebeef5;
  background: #409eff;
  color: #ffffff;
  padding-left: 0.2rem;
  height: 0.7rem;
  font-size: 18px;
}
.el-collapse-item__wrap {
  width: 98%;
  margin-left: 1%;
}
.el-collapse-item__content {
  padding: 0.1rem 0 0.4rem;
}
.info-view {
  width: 100%;
}
.info-view tr td {
  padding: 0 0.1rem;
}
.info-view tr td:first-child {
  width: 32.5%;
  text-align: right;
  font-weight: bold;
  line-height: 30px;
}
.mint-popup-bottom {
  width: 100%;
}
.picker-toolbar-title {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: 40px;
  line-height: 0.8rem;
  font-size: 16px;
  color: #000;
}
.picker-items .picker-slot {
  font-size: 0.26rem;
}
.picker-items .picker-slot:nth-child(odd) {
  width: 45%;
}
.submit-apply {
  margin: 10px auto 0;
  line-height: 0.8rem;
  height: 0.8rem;
  width: 100%;
  font-size: 0.3rem;
  font-weight: bold;
  color: #736b6b;
}
</style>


