<template>
  <div class="hrss-per social-info" v-loading="loading">
    <div class="hrss-search">
      <el-input placeholder="缴费期间" prefix-icon="el-icon-search" v-model="selectDate" readonly @click.native="selectDatePopup=true" size="medium">
        <i slot="suffix" class="el-input__icon el-icon-caret-bottom"></i>
      </el-input>
      <mt-popup v-model="selectDatePopup" closeOnClickModal="true" position="bottom">
        <mt-picker :slots="dataSlots" @change="onValuesChange" showToolbar valueKey="CAL_PRD_DESCR">
          <div class="picker-toolbar-title">
            <div class="usi-btn-cancel" @click="selectDatePopup=!selectDatePopup">取消</div>
            <div class="">缴费期间</div>
            <div class="usi-btn-sure" @click="getSalary">确定</div>
          </div>
        </mt-picker>
      </mt-popup>
    </div>
    <el-collapse v-model="activeNames">
      <el-collapse-item title="社保合计" name="1">
        <ul class="hrss-item-ul">
          <li>
            <span>个人缴费金额（元）</span>
            <span class="hrss-item-right">{{socData.GC_SUM_TOTAL | number}}</span>
          </li>
          <li>
            <span>个人补缴金额（元）</span>
            <span class="hrss-item-right">{{socData.GC_SUM_TOTAL1 | number}}</span>
          </li>
          <li>
            <span>个人金额小计（元）</span>
            <span class="hrss-item-right">{{socData.GC_SUM_TOTAL2 | number}}</span>
          </li>
          <li>
            <span>公司缴费金额（元）</span>
            <span class="hrss-item-right">{{socData.GC_SUM_TOTAL3 | number}}</span>
          </li>
          <li>
            <span>公司补缴金额（元）</span>
            <span class="hrss-item-right">{{socData.GC_SUM_TOTAL4 | number}}</span>
          </li>
          <li>
            <span>公司金额小计（元）</span>
            <span class="hrss-item-right">{{socData.GC_SUM_TOTAL5 | number}}</span>
          </li>
        </ul>
      </el-collapse-item>
      <el-collapse-item title="社保明细" name="2">
        <ul class="hrss-item-ul" v-if="socData.SI_DETAIL.length > 0">
          <li v-for="(social,index) in socData.SI_DETAIL" :key="index">
            <div class="hrss-item-table social-item" :class="{'active':social.show == true}" @click="handleItemClick(social)">
              <div>
                <span>社会保险类型</span>
                <span class="hrss-item-right">{{social.CG_SI_TYPE}}</span>
              </div>
              <div>
                <span>个人金额小计（元）</span>
                <span class="hrss-item-right">{{social.GC_SUM_TOTAL6}}</span>
              </div>
              <div>
                <span>公司金额小计（元）</span>
                <span class="hrss-item-right">{{social.GC_SUM_TOTAL7}}</span>
              </div>
              <div>
                <span>个人缴费基数（元）</span>
                <span class="hrss-item-right">{{social.CG_PAY_BASE_PERS | number}}</span>
              </div>
              <div>
                <span>个人缴费比例（%）</span>
                <span class="hrss-item-right">{{social.CG_PAY_RATE_PERS}}%</span>
              </div>
              <div>
                <span>个人缴费金额（元）</span>
                <span class="hrss-item-right">{{social.CG_PAY_AMT_PERS | number}}</span>
              </div>
              <div>
                <span>个人补缴金额（元）</span>
                <span class="hrss-item-right">{{social.GC_FUND_NBACK_P | number}}</span>
              </div>
              <div>
                <span>公司缴费基数（元）</span>
                <span class="hrss-item-right">{{social.CG_PAY_BASE_CO | number}}</span>
              </div>
              <div>
                <span>公司缴费比例（%）</span>
                <span class="hrss-item-right">{{social.CG_PAY_RATE_CO}}%</span>
              </div>
              <div>
                <span>公司缴费金额（元）</span>
                <span class="hrss-item-right">{{social.CG_PAY_AMT_CO | number}}</span>
              </div>
              <div>
                <span>公司补缴金额（元）</span>
                <span class="hrss-item-right">{{social.GC_FUND_NBACK_C | number}}</span>
              </div>
              <div>
                <span>社会保险缴纳地</span>
                <span class="hrss-item-right">{{social.GC_SI_SIL_DESC}}</span>
              </div>
              <div>
                <span>期间 ID</span>
                <span class="hrss-item-right">{{social.CAL_PRD_ID}}</span>
              </div>
              <div>
                <span>补缴说明</span>
                <span class="hrss-item-right">{{social.GC_FUND_SBACK_CMN}}</span>
              </div>
            </div>
            <div class="hrss-item-table__more" :class="{'active':social.show == true}">
              <i class="el-icon-arrow-down" />
            </div>
          </li>
        </ul>
        <span v-if="socData.SI_DETAIL.length == 0" class="no-data">暂无数据</span>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import filter from "@/assets/filter";
import "@/assets/styles/perFunc.css";
export default {
  data() {
    return {
      activeNames: ["1", "2"],
      prdType: "SOC",
      selectDatePopup: false,
      selectDate: "",
      dataSlots: [],
      slotsSelect: [],
      personal: {},
      socData: { SI_DETAIL: [] },
      loading: false
    };
  },
  methods: {
    onValuesChange(picker, values) {
      this.slotsSelect = values;
    },
    getSalary() {
      if (this.slotsSelect.length > 0)
        this.selectDate = this.slotsSelect[0].CAL_PRD_DESCR;
      this.selectDatePopup = false;
      this.getSocList();
    },
    handleRowClick(row, event, column) {
      this.$refs.socialRef.toggleRowExpansion(row);
    },
    handleItemClick(e) {
      this.$set(e, "show", !e.show);
    },
    getPrdList() {
      let p = {
        TOKEN: this.getCookie("token"),
        OPRID: this.getCookie("oprid"),
        PRD_TYPE: this.prdType
      };
      this.$http
        .post("/PSIGW/RESTListeningConnector/PSFT_HR/PayRoll_Period.v1", p)
        .then(res => {
          if (res.body.status == "0") {
            let d = res.body.JsonData;
            if (d.length > 0) {
              this.dataSlots = [{ values: d }];
              this.slotsSelect = [d[0]];
              this.getSalary();
            }
          }
        });
    },
    getSocList() {
      this.loading = true;
      let p = {
        TOKEN: this.getCookie("token"),
        OPRID: this.getCookie("oprid"),
        PRD_TYPE: this.prdType,
        CALPRDID: this.slotsSelect[0].CAL_PRD_ID
      };
      this.$http
        .post("/PSIGW/RESTListeningConnector/PSFT_HR/PAYROLL_SELECT.v1", p)
        .then(res => {
          if (res.body.status == "0") {
            this.socData = res.body.JsonData;
          }
          this.loading = false;
        })
        .catch(e => {
          this.loading = false;
        });
    }
  },
  created() {
    this.personal = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getPrdList();
  }
};
</script>

<style scoped>
</style>



