<template>
  <div class="recruit">
    <div class="recruit-search">
      <el-input placeholder="搜索" prefix-icon="el-icon-search" v-model="searchModel.KEYWORDS" height="34" @keyup.enter.native="handleSearch" @blur="handleSearch">
      </el-input>
    </div>

    <ul v-if="recruitList.length > 0" v-loading="loading" v-infinite-scroll="loadMore" infinite-scroll-disabled="lastLoad" infinite-scroll-distance="40">
      <li v-for="item in recruitList" :key="item.ID" class="show-list" @click="handleNewClick(item.ID)">
        <span class="show-title">{{item.TITLE}}</span>
        <span class="show-date">{{item.DATE}}</span>
      </li>
    </ul>
    <span class="no-data" v-if="recruitList.length == 0">暂无数据</span>
  </div>
</template>

<script>
import { Indicator } from "mint-ui";
export default {
  data() {
    return {
      searchModel: {
        TOKEN: this.getCookie("token"),
        OPRID: this.getCookie("oprid"),
        KEYWORDS:"",
        START:0,
        END:20
      },
      showTab: "tab-notice",
      recruitList: [],
      loading: false,
      lastLoad: true,
    };
  },
  methods: {
    handleSearch() {
      this.recruitList = [],
      this.searchModel.START = 0;
      this.searchModel.END = 20;
      this.getRecruitList();
    },
    handleNewClick(id) {
      this.$router.push({ name: "recruitInfo", params: { id: id } });
    },
    getRecruitList() {
      this.loading = true;
      this.lastLoad = true;
      this.$http
        .post("/PSIGW/RESTListeningConnector/PSFT_HR/RecuitList.v1/", this.searchModel)
        .then(function(response) {
          if (response.body.status == "0") {
            this.recruitList = response.body.JsonData;
          }
          this.lastLoad = response.body.JsonData.length != 20
          this.loading = false;
        })
        .catch(function(error) {
          this.loading = false;
          console.log(error);
        });
    },
    loadMore() {
      if(this.lastLoad) return;
      this.searchModel.START += 20;
      this.searchModel.END += 20;
      this.getRecruitList();
    }
  },
  created(){
    this.getRecruitList();
  }
};
</script>

<style>
.recruit {
  background: rgba(255, 255, 255, 1);
  padding-bottom: 55px;
}
.recruit-search {
  padding: 10px 15px 20px;
  background: #007bfe;
}
.recruit-search .el-input__inner {
  height: 34px;
}
.recruit-search .el-input__icon {
  line-height: 34px;
}
.recruit-search .mint-navbar {
  background-color: transparent;
}
.recruit-search .mint-tab-item-label {
  font-size: 15px;
}
.recruit-search .mint-navbar .mint-tab-item {
  height: 21px;
  color: rgba(255, 255, 255, 1);
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  line-height: 21px;
  padding: 20px 0 0 0;
  margin-bottom: 0px;
}
.recruit-search .mint-navbar .mint-tab-item.is-selected {
  border-bottom: 3px solid rgba(255, 255, 255, 1);
  font-family: PingFang-SC-Bold;
  font-weight: bold;
}
.show-list {
  padding-top: 15px;
  height: 55px;
  margin-left: 10px;
  border-bottom: 2px solid #eeeeee;
}
.show-list .show-title {
  display: block;
  height: 21px;
  font-size: 15px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(102, 102, 102, 1);
  line-height: 21px;
}
.show-list .show-date {
  display: block;
  height: 17px;
  font-size: 12px;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(153, 153, 153, 1);
  line-height: 17px;
}
.no-data {
  display: inherit;
  margin-top: 10px;
  text-align: center;
}
</style>


