			handleIconClick1() {
				var values = this.state1.replace(/\s+/g, "");;
				var patrn = /^[0-9]*$/;
				if (!patrn.test(values)&&values.length<2){
					this.$messagebox.alert('请输入不小于5位数字的员工ID或不小于2位中文的员工名称')
