				this.$http.post('http://1.180.3.61:8766/SSCDEV/PSIGW/RESTListeningConnector/PSFT_HR/Magnifier.v1/', {
					TOKEN: 'hrssc.yili.com',
					OPRID:'OTAwMDAwMDI=',
