<template>
  <div>
    <div class="func-dev" v-if="personal.length > 0">
      <div class="func-title" style="background:rgba(88,181,48,1);">
        <span>个人应用</span>
      </div>
      <div class="func-icon" ref="funcMe">
        <ul>
          <li v-for="f in personal" :key="f.id" @click="handleMenusClick(f.url)">
            <img :src="require('@/assets/icon/'+f.name+'.png')">
            <span>{{f.name}}</span>
          </li>
        </ul>
      </div>
      <img class="func-more" v-if="pMore" :class="pMoreClass?'':'transform'" :src="require('@/assets/img/pMore.png')">
    </div>
    <div class="func-dev" v-if="work.length > 0">
      <div class="func-title" style="background:rgba(0,134,205,1);">
        <span>工作应用</span>
      </div>
      <div class="func-icon" ref="funcWork">
        <ul>
          <li v-for="f in work" :key="f.id" @click="handleMenusClick(f.url)">
            <img :src="require('@/assets/icon/'+f.name+'.png')">
            <span>{{f.name}}</span>
          </li>
        </ul>
      </div>
      <img class="func-more" v-if="wMore" :class="wMoreClass?'':'transform'" :src="require('@/assets/img/wMore.png')">
    </div>
    <div class="func-dev" v-if="other.length > 0">
      <div class="func-title" style="background:rgba(245,166,35,1);">
        <span>其他应用</span>
      </div>
      <div class="func-icon">
        <ul>
          <li v-for="f in other" :key="f.id" @click="handleMenusClick(f.url)">
            <img :src="require('@/assets/icon/'+f.name+'.png')">
            <span>{{f.name}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      func: [
        {
          id: 1,
          name: "个人信息",
         // url: "resume"
        },
        {
          id: 2,
          name: "我的工资",
          //url: "salary"
        },
        {
          id: 3,
          name: "我的社保",
         // url: "social"
        },
        {
          id: 4,
          name: "我的福利",
        //  url: "welfare"
        },
        {
          id: 5,
          name: "我的考勤",
         // url: "attendance"
        },
        {
          id: 6,
          name: "我的个税",
         // url: "ratepaying"
        },
        {
          id: 7,
          name: "利益关系",
         // url: ""
        },
        {
          id: 8,
          name: "流程中心",
        //  url: "myagency"
        },
        {
          id: 9,
          name: "问题中心",
         // url: ""
        },
        {
          id: 10,
          name: "我的下属",
         // url: "subordinate"
        },
        {
          id: 11,
          name: "工作信息",
        //  url: "enterprise"
        },
        {
          id: 12,
          name: "招贤纳士",
          url: "recruit"
        },
        {
          id: 13,
          name: "新闻中心",
          url: "news"
        }
      ],
      personal: [],
      pMore: false,
      pMoreClass: true,
      work: [],
      wMore: false,
      wMoreClass: true,
      other: [],
      funcList: []
    };
  },
  watch: {
    funcList(v) {
      this.personal = v.filter(i => i.id <= 7);
      this.work = v.filter(i => i.id <= 11 && i.id > 7);
      this.other = v.filter(i => i.id > 11);
    }
  },
  methods: {
    handleMenusClick(url) {
      if (url) {
        this.$router.push({ path: url });
      }
    },
    handelShowWidth() {
      let funcMe = this.$refs.funcMe;
      this.pMore = funcMe.scrollWidth > funcMe.clientWidth;
      if (this.pMore) {
        funcMe.addEventListener(
          "scroll",
          i => {
            this.pMoreClass = !(
              i.target.clientWidth + i.target.scrollLeft ==
              i.target.scrollWidth
            );
          },
          false
        );
      }

      let funcWork = this.$refs.funcWork;
      this.wMore = funcWork.scrollWidth > funcWork.clientWidth;
      if (this.wMore) {
        funcWork.addEventListener(
          "scroll",
          i => {
            this.wMoreClass = !(
              i.target.clientWidth + i.target.scrollLeft ==
              i.target.scrollWidth
            );
          },
          false
        );
      }
    },
    getFuncList() {
      let p = {
        TOKEN: this.getCookie("token"),
        OPRID: "PS" //this.getCookie("oprid")
      };
      this.$http
        .post(
          "http://1.180.3.61:8766/SSCDEV/PSIGW/RESTListeningConnector/PSFT_HR/FounctionList.v1/",
          p
        )
        .then(function(response) {
          if (response.body.status == "0") {
            let f = [];
            response.body.JsonData.forEach(e => {
              f.push(this.func.filter(i => i.id == e.COTEGORY_ID)[0]);
            });
            this.funcList = f;
          } else {
            this.$messagebox.alert(response.body.msg);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  },
  created() {
    this.personal = this.func.filter(i => i.id <= 7);
    this.work = this.func.filter(i => i.id <= 11 && i.id > 7);
    this.other = this.func.filter(i => i.id > 11);
    this.getFuncList();
  },
  mounted() {
    this.handelShowWidth();
  }
};
</script>

<style scoped>
.func-dev {
  /* width: 100%; */
  margin: 0;
  height: 1.6rem;
  border-radius: 0.1rem;
  background-color: #fff;
  margin-top: 0.2rem;
  overflow: hidden;
}
.func-dev .func-title {
  float: left;
  width: 0.8rem;
  height: 1.44rem;
  border-radius: 0.1rem 0 0 0.1rem;
  padding-top: 0.16rem;
}
.func-dev .func-title span {
  display: block;
  width: 0.26rem;
  height: 1.28rem;
  font-size: 0.26rem;
  font-family: PingFang-SC-Bold;
  font-weight: bold;
  color: rgba(255, 255, 255, 1);
  line-height: 0.32rem;
  margin: 0 auto;
}
.func-icon {
  float: left;
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  max-width: 80%;
}
.func-dev ul {
  height: 1.6rem;
  display: inline-block;
}
.func-dev ul li {
  list-style: none; /* 将默认的列表符号去掉 */
  padding: 0; /* 将默认的内边距去掉 */
  margin: 0; /* 将默认的外边距去掉 */
  display: inline-block;
  text-align: center;
  width: 1.5rem;
  height: 1.6rem;
}
.func-dev ul li img {
  width: 0.5rem;
  height: 0.5rem;
  display: block;
  margin: 17px auto 0;
}
.func-dev ul li span {
  height: 0.34rem;
  font-size: 0.24rem;
  font-family: PingFang-SC-Medium;
  font-weight: 500;
  color: rgba(153, 153, 153, 1);
  line-height: 0.34rem;
}
.func-more {
  width: 16px;
  height: 16px;
  margin: 32px 0 0 5px;
}
.func-more.transform {
  transform: rotate(180deg);
}
</style>


